.row {
    color: white;
    margin-left: 20px;
  }
  
  .row_posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
  }
  .row_posters::-webkit-scrollbar {
    display: none; 
  }
  .row_poster {
    max-height: 100px;
    object-fit: contain;
    transition: transform 0.2s;
    margin-right: 10px;
  }
  
  .row_poster:hover {
    transform: scale(1.09);
  }
  
  .row_posterLarge {
    max-height: 250px;
  }
  